<template>
	<view class="publish_form">
		<view class=" pad-lr-30">
			<view class="item flex-center" @click="openAreaPicker(1)">
				<text class="f-32 color-red">*</text>
				<text class="f-32 color-3 w_120 mar-l-10">出发地</text>
				<text class="f-32 color-1 flex-grow mar-l-30">{{postData.start_address}}</text>
				<image src="../../static/img/icon_arrow.png" class="arrow mar-l-20"></image>
			</view>
			<view class="item flex-center" @click="openAreaPicker(2)">
				<text class="f-32 color-red">*</text>
				<text class="f-32 color-3 w_120 mar-l-10">目的地</text>
				<text class="f-32 color-1 flex-grow mar-l-30">{{postData.end_address}}</text>
				<image src="../../static/img/icon_arrow.png" class="arrow mar-l-20"></image>
			</view>
			<view class="item flex" @click="openAreaPicker(3)" v-if="type==2">
				<text class="f-32 color-fff">*</text>
				<text class="f-32 color-3 w_120 mar-l-10">途径地</text>
				<text class="f-32 color-1 flex-grow mar-l-30">{{channel_city_name.join(',')}}</text>
				<image src="../../static/img/icon_arrow.png" class="arrow mar-l-20"></image>
			</view>
			<view class="item flex-center" @click="openTimePicker()">
				<text class="f-32 color-red">*</text>
				<text class="f-32 color-3 w_120 mar-l-10">出发时间</text>
				<text class="f-32 color-1 flex-grow mar-l-30">{{postData.depart_time}}</text>
				<image src="../../static/img/icon_arrow.png" class="arrow mar-l-20"></image>
			</view>
			<view class="item flex-center" @click="isShowServe=true">
				<text class="f-32 color-red">*</text>
				<text class="f-32 color-3 w_120 mar-l-10">信息类型</text>
				<text class="f-32 color-1 flex-grow mar-l-30">{{matterInfo}}</text>
				<image src="../../static/img/icon_arrow.png" class="arrow mar-l-20"></image>
			</view>
			<view class="item flex-center">
				<text class="f-32 color-red">*</text>
				<text class="f-32 color-3 w_120 mar-l-10">联系方式</text>
				<input type="number" v-model="postData.phone" class="f-32 color-1 flex-grow mar-l-30" />
			</view>
			<view class="item flex-center">
				<text class="f-32 color-fff">*</text>
				<text class="f-32 color-3 w_120 mar-l-10">费用估计</text>
				<input type="number" v-model="postData.expense" class="f-32 color-1 flex-grow mar-l-30" />
			</view>
		</view>
		<view class="mark">
			<textarea v-model="postData.desc" placeholder="备注" />
			</view>
		<view class="mar-lr-30 btn">
			<button type="primary" class="bg-blue" @click="publish()">发布</button>
		</view>
		<areaPicker v-show='isShowArea' :valueArea='areaValue' @showChange='areaShowChange' />
		<timePicker v-show='isShowTime' :valueTime='timeValue' @showChange='timeShowChange' />
		<!-- 类型方式 -->
		<matter-layer v-if='isShowServe'
			:type='postData.type' 
			:matter_weight='postData.matter_weight' 
			:matter_type='postData.matter_type' 
			:people_number = 'postData.people_number'
			:disabled = 'false'
			@change='matterChange'>
		</matter-layer>
	</view>
</template>

<script>
	import areaPicker from '../../base/areaPicker.vue'
	import timePicker from '../../base/timePicker.vue'
	import ReleaseApi from '../../apis/release.js'
	import matterLayer from '../../base/matterLayer.vue'
	export default{
		name:'publish_form',
		components:{areaPicker,timePicker,matterLayer},
		data(){
			return{
				type:1,//1寄 2，带
				isShowArea:false,
				isShowTime:false,
				isShowServe:false,
				areaValue:[0,0,0,0],
				timeValue:[],
				start_value:[0,0,0,0],
				end_value:[0,0,0,0],
				areaType:'',
				channel_city_name:[],
				postData:{
					start_address:'',
					end_address:'',
					start_city_id:'',
					end_city_id:'',
					channel_city_id:[],//途径城市id
					depart_time:'',
					type:'',//10载人、20载物
					people_number:0,//人数
					matter_type:'',//物品类型
					matter_weight:'',//物品重量
					phone:'',
					expense:'',//费用
					desc:''
				},
				weightList:[],
				matterTypeList:[]
			}
		},
		onLoad(opt) {
			this.getOption()
			this.type = opt.type
			if(opt.type == 2){
				uni.setNavigationBarTitle({
					title:'发布带物'
				})
			}
		},
		computed:{
			matterInfo(){
				if(this.postData.type == 10 && this.postData.people_number>0){
					return '载人/'+this.postData.people_number+'人'
				}else if(this.postData.type == 20&& this.postData.matter_type && this.postData.matter_weight){
					return '载物/'+this.postData.matter_type+'/'+this.postData.matter_weight
				}else{
					return ''
				}
			}
		},
		methods:{
			matterChange(flag,data){
				this.isShowServe = false;
				if(flag = 'SURE'){
					this.postData = Object.assign({},this.postData,data)
				}
			},
			getOption(){
				ReleaseApi.option().then(res=>{
					this.weightList = res.weight
					this.matterTypeList = res.stdmode
				})
			},
			changeTypeCarry(type){
				this.postData.type = type
			},
			changePeople(type){
				let val = type == 1?this.postData.people_number-1:this.postData.people_number+1
				if(val<0){
					return
				}
				this.postData.people_number = val
			},
			setMatter(val){
				this.postData.matter_type = val
			},
			serWeight(val){
				this.postData.matter_weight = val
			},
			sureServeType(){
				if(!this.postData.type){
					this.$util.showToast('请选择载人或载物')
					return
				}
				if(this.postData.type==10 && this.postData.people_number == 0){
					this.$util.showToast('请输入载人数量')
					return
				}
				if(this.postData.type==20 && this.postData.matter_type == ''){
					this.$util.showToast('请选择物品类型')
					return
				}
				if(this.postData.type==20 && this.postData.matter_weight == ''){
					this.$util.showToast('请选择物品重量')
					return
				}
				this.isShowServe = false
			},
			publish(){
				let data = this.postData
				if(!data.start_address){
					this.$util.showToast('请输入出发地')
					return
				}
				if(!data.end_address){
					this.$util.showToast('请输入目的地')
					return
				}
				// if(data.channel_city_id.length==0 && this.type==2){
				// 	this.$util.showToast('请输入途经地')
				// 	return
				// }
				if(!data.depart_time){
					this.$util.showToast('请选择出发时间')
					return
				}
				if(!data.type){
					this.$util.showToast('请选择信息类型')
					return
				}
				if(!data.phone){
					this.$util.showToast('请输入联系方式')
					return
				}
				// if(!data.expense){
				// 	this.$util.showToast('请输入预计费用')
				// 	return
				// }
				ReleaseApi.release(data,this.type).then(res=>{
					this.$util.showToast('发布成功','success')
					let url = this.postData.type==10?'/pages/rent/index':'/pages/carry/index'
					setTimeout(()=>{
						uni.switchTab({
							url
						})
					},1000)
					
				})
			},
			areaShowChange(flag, value, id, name,fullname) {
				this.isShowArea = false
				if (flag == 'SURE') {
					this.setAreaValue(this.areaType,value, id,fullname)
				}
			},
			timeShowChange(flag, value, name) {
				this.isShowTime = false
				if (flag == 'SURE') {
					this.postData.depart_time = name;
					this.timeValue = value
				}
			},
			openAreaPicker(type) {
				this.areaType = type
				this.areaValue = this.getAreaValue(type)
				this.$nextTick(()=>{
					this.isShowArea = true
				})
			
			},
			openTimePicker() {
				this.isShowTime = true
			},
			getAreaValue(type){
				if(type == 1){
					return this.start_value
				} else if(type == 2){
					return this.end_value
				} else{
					return [0,0,0,0]
				}
			},
			setAreaValue(type,value, id,fullname){
				console.log(value, id,fullname)
				if(type == 1){
					this.start_value = value
					this.postData.start_city_id=id
					this.postData.start_address=fullname
				} else if(type == 2){
					this.end_value = value
					this.postData.end_city_id=id
					this.postData.end_address=fullname
				} else{
					if(this.postData.channel_city_id.indexOf(id)>-1){
						this.$util.showToast('已添加')
						return
					}
					this.postData.channel_city_id.push(id)
					this.channel_city_name.push(fullname)
				}
			}
		}
	}
</script>

<style lang="less" scoped>
	.type_serve{
		position: absolute;
		left: 0;
		right: 0;
		bottom: 0;
		background: #FFFFFF;
		border-radius: 20rpx 20rpx 0rpx 0rpx;

	}
	.publish_form{
		min-height: 100vh;
		background: #fff;
	}
	.item{
		// height: 94rpx;
		padding: 27rpx 0;
		border-bottom: #E6E6E6 solid 1rpx;
		text-align: right;
		.w_120{
			width: 135rpx;
			flex-shrink: 0;
			text-align: left;
		}
	}
	.mark{
		border-top: #F5F5F5 solid 10rpx;
		padding: 20rpx 30px;
		textarea{
			font-size: 32rpx;
			color: #333;
			height: 240rpx;
			width: 100%;
			line-height: 1.5;;
		}
	}
	.btn{
		margin-top: 50rpx;
	}
	.icon_radio{
		width: 38rpx;
		height: 38rpx;
	}
	.count_btn{
		width: 30rpx;
		height: 30rpx;
		background: #F2F2F2;
		border-radius: 15rpx;
		line-height: 28rpx;
		text-align: center;
		font-size: 26rpx;
		font-weight: 600;
		color: #333333;
	}
	.count_person{
		width: 50rpx;
		text-align: center;
		margin: 0 10rpx;
	}
	.list_option{
		font-size: 0;
		margin-top: 10rpx;
		text{
			width: 130rpx;
			height: 50rpx;
			border: 1rpx solid #D9D9D9;
			border-radius: 8rpx;
			font-size: 24rpx;
			font-weight: 500;
			color: #1A1A1A;
			text-align: center;
			line-height: 48rpx;
			margin-right: 10rpx;
			margin-top: 10rpx;
			display: inline-block;
			vertical-align: top;
			&.on{
				border-color: #0493FF;
				color: #0493FF;
			}
		}
	}
</style>
